import React, { Component } from 'react';
import { Select } from 'antd';
import { objectEquals } from 'utils/functionUtil';

const { Option } = Select;
export default class SelectAjax extends Component {
  static defaultProps = {
    keyValue: 'id',
    keyLabel: 'label',
    allowClear: true,
    allowGetData: true,
  };
  state = {
    data: [],
    loading: false,
  };

  componentDidMount = () => {
    this.actionGetData(this.props);
  };

  componentWillReceiveProps = nextProps => {
    if (!objectEquals(nextProps.params, this.props.params)) {
      this.actionGetData(nextProps);
    }
  };
  removeValue = () => {
    const { name: fieldName } = this.props['data-__field'];
    const { setFieldsValue, value } = this.props;
    if (value && setFieldsValue) {
      setFieldsValue({ [fieldName]: undefined });
    }
  };
  actionGetData = async (props = {}) => {
    this.removeValue();
    const { allowGetData, params } = props;
    if (!allowGetData) {
      this.setState({
        data: [],
      });
      return;
    }
    this.setState({
      loading: true,
    });
    const res = await this.props.service(params);
    const data = res.data.items;
    this.setState({
      data,
      loading: false,
    });
  };

  render() {
    const { data, loading } = this.state;
    const { keyValue, keyLabel } = this.props;
    return (
      <Select
        showSearch
        filterOption={(input, option) =>
          option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
        }
        loading={loading}
        {...this.props}
      >
        {data.map(item => (
          <Option key={item.id} value={item[keyValue]}>
            {item[keyLabel]}
          </Option>
        ))}
      </Select>
    );
  }
}



